.App {
  text-align: center;
}

.App-header {
  height: 60px;
  line-height: 60px;
  text-align: left;
  background-color: #222;
  color: white;
}

.App-header img {
  width: 80px;
  height: 60px;
}
.App-header h1 {
  vertical-align: top;
  display: inline-block;
  padding: 0px;
  margin: 0px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.tuts {
  color: #fff;
}

.title {
  color: #737373;
  font-family: Helvetica;
  font-size: 32px;
}

.game-board {
  outline: none;
  background-color: #19181a;
  background-image: url(./img/circuit-board.svg);
  position: relative;
  margin: 20px auto;
  border-left: 10px solid #ccc;
  border-bottom: 10px solid #b3b3b3;
  border-top: 10px solid #ccc;
  border-right: 10px solid #b3b3b3;
}

.snake-head {
  z-index: 1;
  width: 32px;
  height: 32px;
  position: absolute;
  font-size: 32px;
}

.snake-body {
  width: 32px;
  height: 32px;
  position: absolute;
  font-size: 28px;
}

.food {
  width: 32px;
  height: 32px;
  position: absolute;
  font-size: 32px;
}

.start-button {
  width: 100%;
  position: relative;
  display: inline-block;
  text-align: center;
  margin-top: 300px;
}

.start-button .title {
  margin-bottom: 10px;
}

.start-button button {
  padding: 10px 40px;
  background-color: yellow;
  font-size: 32px;
  border-radius: 4px;
  cursor: pointer;
}

.score-display {
  padding: 10px 5px;
  text-align: left;
  font-family: monospace;
  color: #fff;
  font-size: 28px;
  position: absolute;
  top: 0px;
  right: 0px;
  min-width: 200px;
  min-height: 20px;
}

.game-over {
  border: 5px solid #8f8f8f;
  border-radius: 3px;
  margin-top: 15%;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  height: 300px;
  background-color: #111112;
  z-index: 3;
  position: relative;
}
.game-over .fog {
  position: fixed;
  top: 0px;
  bottom: 0px;
  right: 0px;
  left: 0px;
  background: #000;
  opacity: 0.3;
  z-index: 2;
}

.game-over .text {
  font-size: 42px;
  color: #ddd;
  font-family: monospace;
  margin-top: 15%;
  z-index: 3;
  position: relative;
}

.game-over button {
  padding: 10px 100px;
  background-color: yellow;
  font-size: 32px;
  border-radius: 4px;
  cursor: pointer;
}

.game-over .btn-holder {
  padding-top: 10px;
}



